<template>
  <v-container
    fluid
    grid-list-md>
    <v-layout
      row
      wrap>
      <v-flex md12>
        <material-card
          color="primary"
          title="Cadastro de Usu√°rio">
          <v-form
            v-model="valid"
            lazy-validation
            @submit.prevent="validateBeforeSubmit">
            <v-card class="elevation-0">
              <v-card-text>
                <v-layout
                  row
                  wrap>
                  <v-flex md12>
                    <v-autocomplete
                      v-validate="'required'"
                      v-model="form.roles"
                      :items="roles"
                      :error-messages="errors.collect('role')"
                      label="Perfil de Acesso"
                      name="role"
                      item-value="id"
                      item-text="nome"
                      data-vv-name="Perfil de Acesso"
                      multiple
                      chips
                      required
                    />
                  </v-flex>
                  <v-flex md6>
                    <v-autocomplete
                      v-validate="requiredEmpresaLoja"
                      v-model="form.empresaId"
                      :items="empresas"
                      :error-messages="errors.collect('Empresa')"
                      data-vv-name="Empresa"
                      label="Empresa"
                      name="empresa"
                      item-value="id"
                      item-text="nome"
                      chips
                      @change="changeEmpresa"
                    />
                  </v-flex>
                  <v-flex md6>
                    <v-autocomplete
                      v-validate="requiredEmpresaLoja"
                      v-model="form.lojas"
                      :items="lojas"
                      :error-messages="errors.collect('Loja')"
                      data-vv-name="Loja"
                      label="Loja"
                      name="lojas"
                      item-value="id"
                      item-text="descricao"
                      multiple
                      chips
                    />
                  </v-flex>
                  <v-flex md6>
                    <v-text-field
                      v-validate="'required'"
                      v-model="form.nome"
                      :error-messages="errors.collect('Nome Usu√°rio')"
                      label="Nome Usu√°rio"
                      data-vv-name="Nome Usu√°rio"
                      required
                    />
                  </v-flex>
                  <v-flex md6>
                    <v-text-field
                      v-validate="'required|email'"
                      v-model="form.email"
                      :error-messages="errors.collect('E-mail')"
                      label="Email"
                      type="email"
                      data-vv-name="E-mail"
                      required
                    />
                  </v-flex>
                  <v-flex md4>
                    <v-text-field
                      v-validate="'required'"
                      v-if="!form.id"
                      v-model="form.login"
                      :error-messages="errors.collect('Login')"
                      label="Login"
                      data-vv-name="Login"
                      class="lowercase"
                      required
                    />
                    <v-text-field
                      v-else
                      v-model="form.login"
                      label="Login"
                      disabled
                    />
                  </v-flex>
                  <v-flex md4>
                    <v-text-field
                      v-validate="'required'"
                      v-if="!form.id"
                      ref="senha"
                      v-model="form.senha"
                      :error-messages="errors.collect('Senha')"
                      :append-icon="showPasswd ? 'mdi-eye': 'mdi-eye-off'"
                      :rules="[rulesPasswd.required, rulesPasswd.min]"
                      :type="showPasswd ? 'text' : 'password'"
                      label="Senha"
                      name="senha"
                      data-vv-name="Senha"
                      hint="M√≠nimo de 6 caracteres"
                      counter
                      @click:append="showPasswd = !showPasswd"
                    />
                    <v-text-field
                      v-else
                      label="Senha"
                      type="password"
                      value="fdsjfla√ßjsdflajdsflkjalkdjflasdjfdlskj"
                      disabled
                    />
                  </v-flex>
                  <v-flex md4>
                    <v-text-field
                      v-validate="'required|confirmed:senha'"
                      v-if="!form.id"
                      v-model="form.confirmaSenha"
                      :error-messages="errors.collect('Confirmar Senha')"
                      label="Confirmar Senha"
                      type="password"
                      name="confirmaSenha"
                      data-vv-name="Confirmar Senha"
                    />
                    <v-text-field
                      v-else
                      label="Confirmar Senha"
                      type="password"
                      value="fdsjfla√ßjsdflajdsflkjalkdjflasdjfdlskj"
                      disabled
                    />
                  </v-flex>
                  <v-flex md12>
                    <v-text-field
                      v-model="form.bloqueioIp"
                      label="Bloqueio de IPs"
                      name="bloqueioIp"
                      hint="Informe os endere√ßos IPs que o usu√°rio poder√° acessar o sistema, separados por <span class='title'>;</span> (ponto e v√≠gula) "
                    />
                  </v-flex>
                  <v-flex md12>
                    <v-checkbox
                      v-model="form.ativo"
                      label="Ativo"
                      color="primary"/>
                  </v-flex>
                </v-layout>
              </v-card-text>
              <v-card-actions>
                <v-btn
                  v-if="form.id"
                  color="accent"
                  @click="openAlterarSenha">Alterar Senha</v-btn>
                <v-spacer/>
                <v-btn
                  outline
                  style="color: black !important"
                  @click="cancelar">Cancelar</v-btn>
                <v-btn
                  :disabled="!valid"
                  :loading="loadingBtn"
                  type="submit"
                  color="primary"
                >Salvar</v-btn>
              </v-card-actions>
            </v-card>
          </v-form>
        </material-card>
      </v-flex>
    </v-layout>
    <core-alterar-senha
      v-if="dialogAlterarSenha"
      :usuario-id="form.id"
      :show="dialogAlterarSenha"
      :close="closeAlterarSenha"
    />
  </v-container>
</template>

<script>
import { UsuarioBusiness, EmpresaBusiness, RoleBusiness, TiposBusiness } from '../../business'
import { ACAO_CLONAR } from '../../constants'

export default {
  metaInfo: {
    titleTemplate: '%s - Usu√°rio'
  },
  data () {
    return {
      form: {
        id: null,
        nome: null,
        email: null,
        login: null,
        senha: '',
        confirmaSenha: '',
        roles: [],
        lojas: [],
        ativo: true,
        empresaId: null,
        bloqueioIp: null
      },
      showPasswd: false,
      valid: true,
      roles: [],
      empresas: [],
      lojas: [],
      loadingBtn: false,
      loadingAlterarSenha: false,
      rulesPasswd: {
        required: value => !!value || 'Required.',
        min: v => v.length >= 6 || 'M√≠nimo de 6 caracteres'
      },
      dialogAlterarSenha: false
    }
  },
  computed: {
    requiredEmpresaLoja () {
      if (this.form.roles && this.roles.length > 0) {
        var role = null
        for (let i = 0; i < this.form.roles.length; i++) {
          role = this.getRoleById(this.form.roles[i])
          if (role.nome === 'CREDIARISTA' || role.nome === 'PROPRIETARIO') {
            return 'required'
          }
        }
      }
      return ''
    }
  },
  beforeMount () {
    let id = this.$route.params.id
    if (id) {
      UsuarioBusiness.getById(id).then(response => {
        this.form = response.data
        if (ACAO_CLONAR === this.$route.params.acao) {
          this.form.id = null
          this.form.login = null
          this.form.senha = null
          this.form.nome = null
          this.form.email = null
        }
        this.carregarLojas(this.form.empresaId)
      })
    }
    RoleBusiness.findAll().then(response => { this.roles = response.data })
    EmpresaBusiness.findAll().then(response => {
      this.empresas = response.data
    })
  },
  methods: {
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.loadingBtn = true
          UsuarioBusiness.salvar(this.form)
            .then(response => {
              this.$root.showSucesso(response.data)
              this.$router.push('/lista-user')
            })
            .catch(error => {
              this.$root.showErro(error.data)
            })
            .finally(() => {
              this.loadingBtn = false
            })
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    },
    cancelar () {
      this.$router.back()
    },
    changeEmpresa (empresaId) {
      this.form.lojas = []
      this.lojas = []
      this.carregarLojas(empresaId)
    },
    carregarLojas (empresaId) {
      if (empresaId) {
        this.form.empresaId = empresaId
        TiposBusiness.getAllLojas(empresaId).then(response => { this.lojas = response.data })
      }
    },
    getRoleById (roleId) {
      return this.roles.filter(role => role.id === roleId)[0]
    },
    openAlterarSenha () {
      this.dialogAlterarSenha = true
    },
    closeAlterarSenha () {
      this.dialogAlterarSenha = false
    }
  }
}
</script>
